<template>
    <div class="xbline5" :ref="ref">

        <svg class="dev-border" :width="width" :height="height">
            <path :stroke="mergedColor[0]" stroke-width="1" style=" fill: none" stroke-linecap="round"
                stroke-linejoin="round" :d="`M 0 ${height / 2} L ${width}, ${height / 2} `" />
            <circle id="myCircle" :cx="`${width / 4}`" :cy="`${height / 2}`" r="3" :fill="mergedColor[0]"></circle>
            <circle id="myCircle" :cx="`${width / 4 * 3}`" :cy="`${height / 2}`" r="3" :fill="mergedColor[0]"></circle>

            <circle id="myCircle" :cx="0" :cy="`${height / 2}`" r="3" :fill="mergedColor[1]">
                <animateMotion :path="`M 0 0 L ${width} 0 Z`" dur="8s" repeatCount="indefinite" />
            </circle>


        </svg>

        <div class="slot-content">
            <slot></slot>
        </div>
    </div>
</template>

<script>
import autoResize from '../../mixin/autoResize';
import { deepMerge, deepClone } from '../../util/index'
export default {
    name: 'Xbline5',
    mixins: [autoResize],
    props: {
        color: {
            default: () => { [] },
            type: Array,
        },
        backgroundColor: {
            type: String,
            default: 'transparent'
        }

    },
    data () {
        return {
            ref: 'xbline-5',
            border: ['left-top', 'right-top', 'left-bottom', 'right-bottom'],
            defaultColor: ['#0fffc0', '#00a08b'],
            mergedColor: [],
        }
    },
    computed: {

    },
    mounted () {
        this.$nextTick(() => {

            this.mergeColor()
        })


    },
    methods: {
        deepClone,
        deepMerge,
        mergeColor () {
            if (this.color && this.color.length) {
                this.mergedColor = this.deepMerge(this.deepClone(this.defaultColor, true), this.color || []);

            } else {
                this.mergedColor = this.defaultColor
            }



        }

    }
};
</script>

<style lang="scss" scoped>
.xbline5 {
    position: relative;
    width: 100%;
    height: 100%;
    border-radius: 6px;

    .slot-content {
        position: relative;
        padding: 20px;
        width: calc(100% - 40px);
        height: calc(100% - 40px);
    }

    .dev-border {
        position: absolute;
        display: block;
    }

    .right-top {
        right: 0px;
        transform: rotateY(180deg);
    }

    .left-bottom {
        bottom: 0px;
        transform: rotateX(180deg);
    }

    .right-bottom {
        right: 0px;
        bottom: 0px;
        transform: rotateX(180deg) rotateY(180deg);
    }

    .border-box-content {
        position: relative;
        width: 100%;
        height: 100%;
    }
}
</style>
