<template>
    <div class="xbline7" :ref="ref">

        <svg class="dev-border" :width="width" :height="height">
            <rect :x="0" :y="`${height / 2}`" :width="width" height="1" :fill="mergedColor[0]">
                <animate attributeName="width" from="0" :to="`${width}`" :dur="`10s`" calcMode="spline" keyTimes="0;1"
                    keySplines=".42,0,.58,1" repeatCount="indefinite" />
            </rect>

            <rect :x="0" :y="`${height / 2}`" :width="width" height="1" :fill="mergedColor[1]">
                <animate :attributeName="'x'" from="0" :to="`${width}`" :dur="`10s`" calcMode="spline" keyTimes="0;1"
                    keySplines="0.42,0,0.58,1" repeatCount="indefinite" />
            </rect>



        </svg>
        <div class="slot-content">
            <slot></slot>
        </div>
    </div>
</template>

<script>
import autoResize from '../../mixin/autoResize';
import { deepMerge, deepClone } from '../../util/index'
export default {
    name: 'Xbline7',
    mixins: [autoResize],
    props: {
        color: {
            default: () => { [] },
            type: Array,
        },
        backgroundColor: {
            type: String,
            default: 'transparent'
        }

    },
    data () {
        return {
            ref: 'xbline7',
            border: ['left-top', 'right-top', 'left-bottom', 'right-bottom'],
            defaultColor: ['#0fffc0', '#00a08b'],
            mergedColor: [],
        }
    },
    computed: {

    },
    mounted () {
        this.$nextTick(() => {

            this.mergeColor()
        })


    },
    methods: {
        deepClone,
        deepMerge,
        mergeColor () {
            if (this.color && this.color.length) {
                this.mergedColor = this.deepMerge(this.deepClone(this.defaultColor, true), this.color || []);

            } else {
                this.mergedColor = this.defaultColor
            }



        }

    }
};
</script>

<style lang="scss" scoped>
.xbline7 {
    position: relative;
    width: 100%;
    height: 100%;
    border-radius: 6px;

    .slot-content {
        position: relative;
        padding: 20px;
        width: calc(100% - 40px);
        height: calc(100% - 40px);
    }

    .dev-border {
        position: absolute;
        display: block;
    }

    .right-top {
        right: 0px;
        transform: rotateY(180deg);
    }

    .left-bottom {
        bottom: 0px;
        transform: rotateX(180deg);
    }

    .right-bottom {
        right: 0px;
        bottom: 0px;
        transform: rotateX(180deg) rotateY(180deg);
    }

    .border-box-content {
        position: relative;
        width: 100%;
        height: 100%;
    }
}
</style>
